*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: black;
}

h1{
  font-size: 2rem;
  margin-bottom: .5rem;
}

.welcome{
  display: flex;
  align-items: center;
  color: white;
  justify-content: center;
  flex-direction: column;
  border: 1px solid white;
  margin: 10rem 5rem;
  font-size: large;
  border-radius: 15px;
  padding: 4rem;
  line-height: 2rem;
}

.quiz{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid white;
  color: white;
  margin: 5rem 5rem;
  font-size: large;
  border-radius: 15px;
  padding: 4rem;
  line-height: 2rem;
}

.quiz button{
  font-size: 1rem;
  margin: .4rem;
  border-radius: 7px;
  cursor: pointer;
}

.questions{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

input{
  cursor: pointer;
}

.review{
  display: flex;
  flex-direction:column;
  align-items: center;
  color: white;
  justify-content: center;
  border: 1px solid white;
  margin: 10rem 5rem;
  font-size: large;
  border-radius: 15px;
  padding: 4rem;
}
.review button{
  width: 18rem;
  margin-left: 5rem;
}

.green{
  display: flex;
  background: #00b300;
  width: 10rem;
  justify-content: center;
}

.purple{
  display: flex;
  background:#993399;
  width: 10rem;
  justify-content: center;
}

.total_quiz{
  height: 100vh;
}

.score{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
  margin: 10rem 5rem;
  border: 1px solid white;
  font-size: 1.3rem;
  border-radius: 15px;
  color: white;
  padding: 4rem;
  line-height: 2rem;
}

.score button{
  cursor: pointer;
  padding: .4rem;
}

.score h1{
    margin-bottom:1.5rem;
  }
  .score h2{
    margin-bottom:2rem;
  }

button{
  font-size: 1.2rem;
  border: 1px solid white;
  padding: .3rem;
  cursor: pointer;
  border-radius: 7px;
  margin: 1rem 0rem;
  margin-right: 3rem;
}

button:hover{
  transform: translateY(-0.2rem);
  transition: all .2s ease-in-out;
}

button:disabled {
  color: white;
  cursor: not-allowed; 
}


.correct{
  color: green;
}

.wrong{
  color: red;
}

@media screen and (max-width:550px){
  h1{
    font-size: 1.5rem;
  }
  h2{
    font-size: 1.25rem;
  }
  body{
font-size: 1rem;
  }
  .review {
    display: flex;
    flex-direction: column;
  }
  .review button{
    margin: 1rem 10rem;
    width: 15rem;
  }
  .score button{
    margin: 1rem 10rem;
  }
  button{
    display: flex;
    flex-direction: column;
    width: 10rem;
    align-items: center;
    justify-content: center;
  }
  .score h1{
    margin-bottom:1.5rem;
  }
  .score h2{
    margin-bottom:2rem;
  }
}

@media screen and (max-width:430px){
  .welcome{
    margin: 2rem 2rem;
    margin-top: 9rem;
    display: flex;
    align-items: center;
    justify-self:center;
  }
  button{
    margin: 1rem 2rem;
  }
  .welcome h1{
    width:15rem;
    padding-left: .8rem;
  }
  p{
    width: 15rem;
    padding-left: .7rem;
  }
  .quiz{
    margin: 2rem 2rem;
    display: flex;
    align-items: center;
    justify-self:center;
  }
  .review{
    margin: 2rem 2rem;
    margin-top: 9rem;
    display: flex;
    align-items: center;
    justify-self:center;
    margin-top: 3rem;
  }
  .review h1{
    width:15rem;
    padding-left: 3rem;
  }
  .review button{
    width: 15rem;
  }
  .score{
    margin: 2rem 2rem;
    margin-top: 9rem;
    display: flex;
    align-items: center;
    justify-self:center;
  }
  .score p{
    padding-left: 2.5rem;
  }
  .score h1{
    margin-bottom:.8rem;
  }
  .score h2{
    margin-bottom:1rem;
  }
}


@media screen and (min-width:1030px){
  .welcome{
    margin:10rem 20rem;
  }
  .quiz{
    margin:10rem 20rem;
    margin-top: 3rem;
  }
  .review{
    margin:10rem 20rem;
    margin-top: 3rem;
  }
  .review button{
    width: 20rem;
  }
  .score{
    margin:10rem 20rem;
  }
  .score h1{
    margin-bottom:1.5rem;
  }
  .score h2{
    margin-bottom:2rem;
  }
}

@media screen and (max-width:320px) {
  .questions{
    width:10rem;
  }
  .review button{
    width:10rem;
  }
  .score h1{
    margin-bottom:1.5rem;
  }
  .score h2{
    margin-bottom:2rem;
  }
}